<template>
  <div>
    <nav class="navigation" ref="nav">
      <ul>
        <li v-for="link in links"><router-link :to="'/'+link">{{link}}</router-link></li>
      </ul>
    </nav>
  </div>

</template>
<script>
  export default {
    data(){
      return{
        links:['Home','About']
      }
    }
  }
</script>


<style scoped  lang="scss">
  /*needed imports*/
  @import '../../assets/scss/variables';
  /*a simple animation*/
  @keyframes slideNavDown {
    0%{
      top: -100px;
      display: none;
    }
    100%{
      top: 0px;
      display: block;
    }
  }
  /*animated class for navigation*/
  .animatedNav{
    position: relative;
    animation: slideNavDown 1s;
  }
  nav{
    background-color: $backgroundBlack;
    ul{
      list-style-type: none;
      padding: 0;
      margin: 0;
    }
    a{
      line-height: 1.5em;
      text-decoration: none;
      text-align: center;
      padding: 20px;
      display: block;
      color: white;
      &:hover{
        background-color: #555;
      }
    }
  }

  @media screen and  (min-width: 500px){
    nav ul{
      display: flex;
      justify-content: flex-start;
    }
    nav li{
      /*flex: 1 1 0;*/
    }
  }
</style>



