import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import about from '@/components/about'
import singlePost from '@/components/singlePost'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/singlePost/:id',
      name: 'viewSingle',
      component: singlePost
    }
  ],
  mode:'history'
})



