<template>
  <div id="formComponent">
    <h3>ADD A POST<span><button class="closeFormBtn" @click="closeEmptyForm">X</button></span></h3>
    <form @submit.prevent="submitNewPost" id="addPostForm">
      <label for="postTitle">Post title:</label>
      <input type="text" v-model="post.title" id="postTitle" placeholder="Enter title">
      <label for="postContent">Post Content:</label>
      <textarea id="postContent" v-model="post.content" placeholder="Enter the content"></textarea>
      <input type="submit" value="Submit">
    </form>
  </div>
</template>
<script>
  import db from '../../databaseConf/firebaseInit'
  export default {
    data(){
      return{
        post:{
          title:'',
          content:''
        }
      }
    },
    methods:{
      closeEmptyForm(){
        this.$emit('popItUp');
      },

      closeIt(){
        this.$emit('closeIt');
      },
      submitNewPost(){
        db.collection('posts').add({
          title: this.post.title,
          content: this.post.content
        }).then(doc=>{
          this.closeIt(doc);
        }).catch(err=>{
          console.log(err);
        });
      }
    }
  }
</script>
<style scoped>
  #addPostForm{
    padding: 17px;
  }
  #postTitle{
    display: block;
    width: 100%;
    margin: 10px auto;
    padding: 17px;
    box-sizing: border-box;
    font-size: large;
    color: #5d5d5d;
  }
  #postContent{
    display: block;
    width: 100%;
    border-radius: 10px;
    padding: 17px;
    box-sizing: border-box;
    font-size: x-large;
  }
  /*Submit form  button*/
  #addPostForm input[type=submit]{
    display: block;
    box-sizing: border-box;
    padding: 17px;
    margin: 25px auto;
    width: 75%;
    background-color: #3e3230;
    color: #cccccc;
    font-size: large;
    border: #3e3230 2px solid;
    cursor: pointer;
  }
  /*target the button that emit the function to close the form in the home parent component*/
  .closeFormBtn{
    background-color: #8a2121;
    float: right;
    color: white;
    padding: 10px;
    border: 2px #8a2121 solid;
  }
  /*Grabbing the whole form component*/
  #formComponent{
    background-color: #5f9ea0;
    padding: 17px;
  }
  *{
    color: #303030;
  }
</style>



