<template>
  <div id="home">
      <div class="content" v-if="loaded">
        <header class="searchBox" ref="header">
          <div>
            <span>
              <img id="boomImg" src="../assets/img/boom1.png" alt="">
            </span>
          </div>
          <h2>When SASS meets vue!!</h2>
          <div id="searchContainer">
            <input  v-model="search" class="textInput" type="text" name="search" id="search" placeholder="Search for your prefered posts!!!!">
            <button id="addPostBtn" type="submit" @click="toggleAddPostForm">Add A Post</button>
          </div>
        </header>
        <transition enter-active-class="animated bounceInDown" leave-active-class="animated bounceOutUp">
          <div class="formContainer" v-if="addPostForm">
            <add-post class="addPostForm" @popItUp="popItUp" @closeIt="fetchOnUpdate"></add-post>
          </div>
        </transition>
        <div class="container main-content">
          <section class="articles">
            <transition-group tag="div" enter-active-class="animated fadeInLeft" leave-active-class="animated fadeOutRight">
              <div class="indivPost" v-for="post in filterPosts"  :key="post.title">
                <h3>{{post.title}} </h3>
                <div>
                  <div>
                    {{post.content.substring(0, 100)+'....'}}
                  </div>

                  <!--<router-link class="readMoreBtn" :to="'/singlePost/'+post._id">Read more..</router-link>-->
                  <button class="readMoreBtn" @click="gotoSinglePost(post.id)">Read More..</button>
                </div>
              </div>
            </transition-group>
          </section>

          <aside class="aside">
            <h4>Posts Outline</h4>
            <ol>
              <li v-for="post in posts">
                {{post.title}}
              </li>
            </ol>
          </aside>
        </div>
      </div>
    <div class="freeSpiner" v-else>
      <div class="sk-fading-circle">
        <div class="sk-circle1 sk-circle"></div>
        <div class="sk-circle2 sk-circle"></div>
        <div class="sk-circle3 sk-circle"></div>
        <div class="sk-circle4 sk-circle"></div>
        <div class="sk-circle5 sk-circle"></div>
        <div class="sk-circle6 sk-circle"></div>
        <div class="sk-circle7 sk-circle"></div>
        <div class="sk-circle8 sk-circle"></div>
        <div class="sk-circle9 sk-circle"></div>
        <div class="sk-circle10 sk-circle"></div>
        <div class="sk-circle11 sk-circle"></div>
        <div class="sk-circle12 sk-circle"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import db from '../databaseConf/firebaseInit'
  import axios from 'axios'
  import addPost from './partials/addPost'


export default {
  name: 'home',
  data () {
    return {
      loaded: false,
      posts:[],
      search:'',//used filtering posts
      addPostForm:false
    }
  },
  computed:{
    filterPosts(){
      return this.posts.filter((post)=>{
        return post.title.toLowerCase().match(this.search.toLowerCase())
      });
    }
  },
  methods:{
    popItUp(){
      this.toggleAddPostForm();
    },

    fetchOnUpdate(){
      db.collection('posts').get().then(querySnapshot=>{
        this.posts = [];
        querySnapshot.forEach(doc=>{
          let tempDoc ={};
          tempDoc.id = doc.id;
          tempDoc.title = doc.data().title;
          tempDoc.content = doc.data().content;
          this.posts.push(tempDoc) ;
        })
      }).then(loader=>{
        this.toggleAddPostForm();
      });
    },
    toggleAddPostForm(){
      this.addPostForm = !this.addPostForm;
    },
    gotoSinglePost(id){
      this.$router.push(`/singlePost/${id}`);
    }
  },
  components:{
    addPost
  },
  created(){
    db.collection('posts').get().then(querySnapshot=>{
      querySnapshot.forEach(doc=>{
        // console.log(doc);
        var post={};
        post.id = doc.id;
        post.title = doc.data().title;
        post.content = doc.data().content;
        this.posts.push(post);
        return true;
      })
    }).then(loader=>{
      this.loaded = true;
    })

  }
}
</script>

<style scoped lang="scss">
  @import '../assets/scss/variables';
  header{
    background-color: $backBlue;
    color: $whiteColor;
    padding: 50px;
    text-align: center;
    font-size: large;
    margin:0;
  }


  /*target searchContainer form*/
  #searchContainer{

    @media screen and (max-width: $smScreens){
      input{
        display: block;
        text-align: center;
        margin: 0 auto;
        box-sizing: border-box;
        max-width: 250px;
      }
      button{
        display: block;
        text-align: center;
        margin: 0 auto;
        box-sizing: border-box;
      }
    }
  }

  /*target articles*/
  .main-content .articles .indivPost{
    background-color: $indigoColor;
    margin: 18px ;
    padding: 17px;
    border-radius: 5px;
    transition: 0.5s;
    &:hover{
      border-left: $backBlue solid 10px;
      transition: 0.5s;
    }
  }
  /*Target add post form*/
  .addPostForm{
    position: fixed;
    bottom:10px;
    right:10px;
    width: 50%;
  }
  .formContainer{
    position: fixed;
    top: 0;
    left:0;
    width: 100%;
    height: 100%;
    background-color: $blackBackground;
  }
  /*image boom*/
  #boomImg{
    height: 100px;
  }

  /*target wider screens*/
  @media screen and (min-width: 768px){
    .main-content{
      display: flex;
    }
    .main-content .articles{
      flex: 2 2 0;
    }
    .main-content .aside{
      flex: 1 1 0;
    }
    .main-content *{
      padding: 5px;

    }
  }
  /*targeting the aside*/
  .aside{
    ol{
      background-color: $blackBackground;
      padding: 50px;
      max-height: 250px;
      overflow: scroll;
    }
    li{
      text-decoration: none;
      font-weight: bold;
      color: $backgroundLightGray;
      margin-bottom: 17px;
      line-height: 1.5em;
    }

  }


  /*spinner*/

  .freeSpiner{
    min-height: 300px;
    margin: 20% auto 5% auto;
  }

  .sk-fading-circle {
    margin: 100px auto;
    width: 40px;
    height: 40px;
    position: relative;
  }

  .sk-fading-circle .sk-circle {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }

  .sk-fading-circle .sk-circle:before {
    content: '';
    display: block;
    margin: 0 auto;
    width: 15%;
    height: 15%;
    background-color: #333;
    border-radius: 100%;
    -webkit-animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
    animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
  }
  .sk-fading-circle .sk-circle2 {
    -webkit-transform: rotate(30deg);
    -ms-transform: rotate(30deg);
    transform: rotate(30deg);
  }
  .sk-fading-circle .sk-circle3 {
    -webkit-transform: rotate(60deg);
    -ms-transform: rotate(60deg);
    transform: rotate(60deg);
  }
  .sk-fading-circle .sk-circle4 {
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
  }
  .sk-fading-circle .sk-circle5 {
    -webkit-transform: rotate(120deg);
    -ms-transform: rotate(120deg);
    transform: rotate(120deg);
  }
  .sk-fading-circle .sk-circle6 {
    -webkit-transform: rotate(150deg);
    -ms-transform: rotate(150deg);
    transform: rotate(150deg);
  }
  .sk-fading-circle .sk-circle7 {
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  .sk-fading-circle .sk-circle8 {
    -webkit-transform: rotate(210deg);
    -ms-transform: rotate(210deg);
    transform: rotate(210deg);
  }
  .sk-fading-circle .sk-circle9 {
    -webkit-transform: rotate(240deg);
    -ms-transform: rotate(240deg);
    transform: rotate(240deg);
  }
  .sk-fading-circle .sk-circle10 {
    -webkit-transform: rotate(270deg);
    -ms-transform: rotate(270deg);
    transform: rotate(270deg);
  }
  .sk-fading-circle .sk-circle11 {
    -webkit-transform: rotate(300deg);
    -ms-transform: rotate(300deg);
    transform: rotate(300deg);
  }
  .sk-fading-circle .sk-circle12 {
    -webkit-transform: rotate(330deg);
    -ms-transform: rotate(330deg);
    transform: rotate(330deg);
  }
  .sk-fading-circle .sk-circle2:before {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s;
  }
  .sk-fading-circle .sk-circle3:before {
    -webkit-animation-delay: -1s;
    animation-delay: -1s;
  }
  .sk-fading-circle .sk-circle4:before {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s;
  }
  .sk-fading-circle .sk-circle5:before {
    -webkit-animation-delay: -0.8s;
    animation-delay: -0.8s;
  }
  .sk-fading-circle .sk-circle6:before {
    -webkit-animation-delay: -0.7s;
    animation-delay: -0.7s;
  }
  .sk-fading-circle .sk-circle7:before {
    -webkit-animation-delay: -0.6s;
    animation-delay: -0.6s;
  }
  .sk-fading-circle .sk-circle8:before {
    -webkit-animation-delay: -0.5s;
    animation-delay: -0.5s;
  }
  .sk-fading-circle .sk-circle9:before {
    -webkit-animation-delay: -0.4s;
    animation-delay: -0.4s;
  }
  .sk-fading-circle .sk-circle10:before {
    -webkit-animation-delay: -0.3s;
    animation-delay: -0.3s;
  }
  .sk-fading-circle .sk-circle11:before {
    -webkit-animation-delay: -0.2s;
    animation-delay: -0.2s;
  }
  .sk-fading-circle .sk-circle12:before {
    -webkit-animation-delay: -0.1s;
    animation-delay: -0.1s;
  }

  @-webkit-keyframes sk-circleFadeDelay {
    0%, 39%, 100% { opacity: 0; }
    40% { opacity: 1; }
  }

  @keyframes sk-circleFadeDelay {
    0%, 39%, 100% { opacity: 0; }
    40% { opacity: 1; }
  }
</style>



